/* =====================
	NAVIGATION
====================*/
/****************
*	1. mixin for showing desktop dropdown
*	2. mobile menu dropper element +
*	3. MAIN NAV (parent wrapper)
*	4. First Level Menu
*	5. Second Level Menu
*	6. Third Level Menu (should only show for mobile when necessary)
*******************/
button.nav-expander {
  display: none;
  position: absolute;
  top: 0;
  right: -35px;
  width: 35px;
  height: 100%;
  cursor: pointer;
  z-index: 1050;
  -webkit-appearance: none;
  border: 0;
  padding: 0;
  background: transparent;
}
button.nav-expander:before,
button.nav-expander:after {
  content: "";
  display: block;
  position: absolute;
  top: 45%;
  right: 0;
  width: 16px;
  height: 2px;
  background: #009bd6;
}
button.nav-expander:after {
  transform: rotate(-90deg);
  transition: all 250ms ease;
}
