==================== */
.hero {
  position: relative;
  display: grid;
  height: clamp(450px, 80vh, min(900px, 70vw));
  grid-template-areas: "stack";
  place-items: center;
}
.hero > * {
  grid-area: stack;
}
.hero__background {
  position: relative;
  width: 100%;
  height: clamp(450px, 80vh, min(900px, 70vw));
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.hero__background:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: #000;
  opacity: 0.5;
